//File Name: Footer.js

//-----------------------------------------------------------------------------------
//General Imports
//-----------------------------------------------------------------------------------
import React, { Component } from "react"
import footerstyle from "../../styles/footer.module.css";

class Footer extends React.Component {

    render() { 

        return(
            <footer className={footerstyle.container}>

                <h1>Start a Converstation!</h1>

                <form action="https://getform.io/f/913fb927-ffc6-4a13-837f-74b6564ca712" method="POST">

                    <input type="text" placeholder='First Name (Required)' required name="first_name"/>
                    <input type="text" placeholder='Last Name (Required)' required name='last_name'/>
                    <input type="text" placeholder='Subject (Optional)' name='subject'/>
                    <textarea type="submit" cols="40" rows="10" required placeholder="Message" name='message'/>
                    <input className={footerstyle.input_submit} type="submit"></input>



                </form>

                <p> &copy; Ryan Fabek 2020</p> 
                
            </footer>
        
        );

    }

}
export default Footer;